.page #inner-content .entry-content > .full-width-background:first-child {
  margin-top: 0;
}

.page #inner-content {
	 
  
    .entry-content > *, .article-header > * {
      .max-central;
    }

    .full-width-background {

      margin: 2em auto;
      padding: 2em 0;
      max-width: 100%;



      > * {
        .max-central;

        &:last-child {
          margin-bottom: 0;
        }
      }





      @media screen and (max-width: @max-central-width) {
        width: 100%;
      }

      > h1, > h2, > h3, > h4 {
        margin-top: 0;
      }
      
      background: @light;

      .colours-bg();
  

      & > h2 {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
        //padding-bottom: 4em;
      }
    }


    .full-width-background + .full-width-background {
      margin-top: -2em;
    }

}


